@import url('https://fonts.googleapis.com/css2?family=Salsa&display=swap');

.reg-card-container {
    /* perspective: 1000px; */
}

.reg-card {
    width: 8vw;
    height: 9vw;
    position: relative;
    transition: transform 0.6s;
}
.hovered {
    cursor: pointer;
}

.reg-front {
    width: 100%;
    height: 100%;
    position: absolute;
}

.reg-front {
    /* transform: rotateY(0deg); */
    /* z-index: 2; Ensure front is above back initially */
}

.reg-card-bg {
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: -1;
}

.reg-header {
    display: flex;
    gap: 4px;
    margin-left: 1.2vw;
    width: 100%;
    align-items: flex-start;
}

.reg-contents {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    padding-top: 1.2vh;
    padding-bottom: 2vh;
    font-family: Salsa;
    /* color: #323232; */
}

.event-img-div {
    width: 5vw;
}

.event-img {
    width: 5vw;
}

.reg-event-name {
    width: 60%;
    font-size: 0.8vw;
}

.event-discr {
    width:8vw;
    font-size: 0.8vw;
    display: flex;
    justify-content: center;
    text-align: center;
}

.event-date {
    font-size: 1.1vw;
}

.buttons-div {
    gap: 5vh;
}

.btn-1, .btn-2 {
    font-family: Salsa;
    color: #323232;
    background: transparent;
    border: 3px ;
    border-radius: 5px;
    width: 8vw;
    font-size: 1.7vw;
}

.btn-1:hover, .btn-2:hover {
    cursor: pointer;
    color: white;
    background-color: #323232;
}
.card-container-mobile{
    display:none;
}
.mobcard{
    margin-top: 0;
    width:100vw;
    height:52vw;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
    /* margin-bottom: -25vh; */

}
/* .mob-card{
    width:80vw;
    display: flex;
    justify-content: center;
} */
.card-bg-mob{
    width:80vw;
    /* height:60vh; */
    z-index: -1;
    position: absolute;
    /* margin-bottom: -25vh; */
}
.contents-mob{
    margin-bottom: 3vh;
    width:80vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-family: Salsa;
}
.event-img-div-mob{
    width:30vw;
}
.event-img-mob{
    width:30vw;
    /* height:30vh; */
}
.title-desc{
    width:70vw;
    display: flex;
    flex-direction: column;
    justify-content: start;
    padding: 0 5vw 0 5vw;
}
.event-name-mob{
    font-size: 5vw;
}
.event-discr-mob{
    font-size:3vw;
    
}
.buttons-div-mob{
    width:70vw;
    display: flex;
    justify-content: space-between;
    padding:0 6vw 7px 5vw;
    margin-top: 1.5vh;
}
.btn-1-mob,.btn-2-mob{
    width:30vw;
    height: 5vh;
    font-family: Salsa;
    font-size: 3.5vw;
    border-radius: 5px;
    border-width: 3px;
    background: transparent;
}
.btn-1-mob:hover,.btn-2-mob:hover{
    background-color: #323232;
    color: white;
}

